.work {
}
.work__wrapper {
  background-color: #FFFFFF;
}
.work__title {
  font-family: 'Oswald';
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
  padding: 0;
  margin: 0;
  margin-bottom: 40px;
}
.work__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.work__item {
  display: flex;
  align-items: center;
  gap: 20px;
}
.work__item-icon-wrapper {
  display: flex;
  margin: 0;
  padding: 0;
  min-width: 60px;
  min-height: 60px;
  background-color: #68B738;
}
.work__item-icon {
  align-self: center;
  margin: auto;
}
.work__item-description {
  margin: 0;
  padding: 0;
  font-family: 'Lato';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}


@media (min-width: $tablet-width) {
  .work__title {
    font-size: 60px;
    line-height: 60px;
  }
  .work__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }
  .work__item {
    display: flex;
    flex-direction: column;
    gap: 31px;
    justify-items: flex-start;
    min-height: 280px;
    width: 250px;
  }
  .work__item-1 {
    background-image: url(../img/main-page/work/1.svg);
    background-repeat: no-repeat;
    background-position: right;
    }
  .work__item-2 {
    background-image: url(../img/main-page/work/2.svg);
    background-repeat: no-repeat;
    background-position: right;
  }
  .work__item-3 {
    background-image: url(../img/main-page/work/3.svg);
    background-repeat: no-repeat;
    background-position: right;
  }
  .work__item-4 {
    background-image: url(../img/main-page/work/4.svg);
    background-repeat: no-repeat;
    background-position: right;
  }
  .work__item-icon-wrapper {
    margin-top: 30px;
    min-width: 80px;
    min-height: 80px;
    align-self: flex-start;
  }
  .work__item-description {
    font-size: 16px;
    line-height: 24px;
    color: #444444;
  }


}

@media (min-width: $desktop-width) {
  .work__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    justify-items: center;
    gap: 0;
  }
}
